import {getSeason, getTime} from "./dateTimePrettificator";
const ClearImage = './vk_weather/img/clear.png';
const RainImage = './vk_weather/img/rain.png';
const MistImage = './vk_weather/img/mist.png';
const NightClearImage = './vk_weather/img/nightClear.png';
const NightRainImage = './vk_weather/img/nightRain.png';
const NightSnowImage = './vk_weather/img/nightSnow.png';
const NightLightRainImage = './vk_weather/img/nightLightRain.png';
const AutumnCloudsImage = './vk_weather/img/autumnClouds.png';
const SpringCloudsImage = './vk_weather/img/springClouds.png';
const SummerCloudsImage = './vk_weather/img/summerClouds.png';
const WinterCloudsImage = './vk_weather/img/winterClouds.png';
const SpringClearImage = './vk_weather/img/springClear.png';

const weatherList: any = {
    'Thunderstorm': '',
    'Drizzle': '',
    'Rain': '',
    'Snow': '',
    'Mist': '',
    'Smoke': '',
    'Haze': '',
    'Dust': '',
    'Fog': '',
    'Sand': '',
    'Ash': '',
    'Squall': '',
    'Tornado': '',
    'Clear': '',
    'Clouds': ''
};


/**
 *     API  
 * @param weather    OpenWeatherMap
 */
export const lookupWeather = (weather: string) => {
    return weatherList[weather];
};

/**
 *        
 * @param deg    
 */
export const toWindDirection = (deg: number) => {
    if (deg >= 330 || deg <= 30) {
        return '';
    }
    else if (deg < 60) {
        return '-';
    }
    else if (deg < 120) {
        return '';
    }
    else if (deg < 150) {
        return '-';
    }
    else if (deg < 210) {
        return '';
    }
    else if (deg < 240) {
        return '-';
    }
    else if (deg < 300) {
        return '';
    }
    else {
        return '-';
    }
};

export const chooseWeatherImage = (weather: any) => {
    if (Number(getTime(weather.dt).hour) > 20 || Number(getTime(weather.dt).hour) < 5) {
        switch (weather.weather[0].main) {
            case 'Drizzle':
            case 'Rain':
                return NightLightRainImage;
            case 'Thunderstorm':
            case 'Squall':
            case 'Tornado':
                return NightRainImage;
            case 'Snow':
                return NightSnowImage;
            case 'Mist':
            case 'Smoke':
            case 'Haze':
            case 'Dust':
            case 'Fog':
            case 'Sand':
            case 'Ash':
                return MistImage;
            case 'Clear':
            case 'Clouds':
                return NightClearImage;
        }
    }
    switch (weather.weather[0].main) {
        case 'Thunderstorm':
        case 'Drizzle':
        case 'Rain':
        case 'Snow':
        case 'Squall':
        case 'Tornado':
            return RainImage;
        case 'Mist':
        case 'Smoke':
        case 'Haze':
        case 'Dust':
        case 'Fog':
        case 'Sand':
        case 'Ash':
            return MistImage;
        case 'Clear':
            switch (getSeason(weather.dt)) {
                case 'spring':
                    return SpringClearImage;
                default:
                    return ClearImage;
            }
        case 'Clouds':
            switch (getSeason(weather.dt)) {
                case 'spring':
                    return SpringCloudsImage;
                case 'summer':
                    return SummerCloudsImage;
                case 'autumn':
                    return AutumnCloudsImage;
                case 'winter':
                    return WinterCloudsImage;
            }
    }
};