enum months {
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    ''
}
enum week {
    '',
    '',
    '',
    '',
    '',
    '',
    ''
}


/**
 *      " "
 * @param dt   UNIX-
 */
export const toNormalDate = (dt: number) => {
    let date = new Date(dt * 1000);
    let day = date.getDate();
    let month = date.getMonth();
    return day + ' ' + months[month];
}


/**
 *        
 * @param dt   UNIX-
 */
export const getTime = (dt: number) => {
    let date = new Date(dt * 1000);
    let hour = date.getHours();
    let minute = date.getMinutes();
    let time = '';
    let dayOfWeek = week[date.getDay()];
    let stringHour = hour.toString();
    let stringMinute = minute.toString();

    if (hour < 5 || hour > 22) { time = '' }
    else if (hour > 18) { time = '' }
    else if (hour > 10) { time = '' }

    if (hour < 10) { stringHour = '0' + hour; }
    if (minute < 10) { stringMinute = '0' + minute; }

    return {
        hour: stringHour,
        minute: stringMinute,
        time: hour + ':' + minute,
        string: time,
        day: dayOfWeek
    };
}

/**
 *       
 * @param dt   UNIX-
 */
export const getSeason = (dt: number) => {
    let date = new Date(dt * 1000);
    let month = date.getMonth();
    if (month === 12 || month <= 1) {
        return 'winter';
    }
    else if (month <= 4) {
        return 'spring';
    }
    else if (month <= 7) {
        return 'summer';
    }
    else { return 'autumn'; }
}

