import React from "react";
import {Panel, View} from "@vkontakte/vkui";

type ErrorViewProps = {
    goBack: () => void;
    history: string[];
    activePanel: string;
};

const ErrorView:React.FC<ErrorViewProps> = ({ goBack, history, activePanel }) => {
    const ErrorImage = './vk_weather/img/error.png';

    return (
        <View
            onSwipeBack={goBack}
            history={history}
            activePanel={activePanel}
        >
            <Panel id='weatherImage'>
                <div className='mainGradient fullHeight aligner'>
                    <img src={ErrorImage}  className='loading' alt={''} />
                    <h1 className='errorMessage faded'>...  :(</h1>
                </div>
            </Panel>
        </View>
    );
};
export default ErrorView;